@import "src/common/styles/sass/variables";
@import "src/common/styles/sass/mixins";
.containerSwitch {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  flex-shrink: 0;
  margin-right: 10px;
  /* The switch - the box around the slider */
  .switch {
    font-size: 17px;
    position: relative;
    display: inline-block;
    width: 3.3em;
    height: 1.6em;
    /* Hide default HTML checkbox */
    & input {
      opacity: 0;
      width: 0;
      height: 0;
    }
  }
  /* The slider */
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: var(--bgColor);
    transition: .4s;
    border-radius: 30px;
    &:before {
      position: absolute;
      content: "";
      height: 1.1em;
      width: 1.1em;
      border-radius: 20px;
      left: 0.3em;
      bottom: 0.3em;
      background-color: var(--textColor);
      transition: .4s;
    }
  }
}

input:checked+.slider {
  background-color: $primaryColor;
}


input:checked+.slider:before {
  transform: translateX(1.5em);
}
